  border-color: #7b68ee;
  border-radius: 15px;
  border-width: .2em;
}

.container-fluid {
  height: 72px;
}

.main {
  height: 100vh;
  background-color: #fafbfc;
  background: linear-gradient(to bottom, #fafbfc 0%, #fafbfc 50%, #f0f0fc 50%, #f0f0fc  100%);
}

.nav-main {
  background-color: #fafbfc;
