}

.nav-collapse {
  align-self: flex-end!important;
}

.btn-submit {
  background-color: #7b68ee;
  font-weight: bold;
  border-radius: 20px;
  border-color: #7b68ee;
  width: 100%;
  font-size: 20px;
}

.inputbar {
  -webkit-appearance: none;
  border: none;
  border-radius: 15px;
  background: #fafbfc;
  box-shadow: 7px 7px 14px #cfcfcc, -7px -7px 14px #ffffff;
}

.form-label {
  font-size: 25px;
